.TodoItem {
  background-color: #FAFAFA;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 1rem;
  padding: 0 8px;
  gap: 8px;
}
  
.TodoItem-p {
  margin: 5%;
  width: calc(100% - 120px);
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
}
.TodoItem-p--complete {
  text-decoration: line-through;
}

.Icon {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 48px;
  width: 48px;
  font-size: 24px;
  font-weight: bold;
}


.Icon-delete {
  right: 0;
}
.Icon-delete:hover {
  color: red;
}

.check-button {
  height: 30px;
  width: 30px;
  border: 1px solid black;
  background-color: white;
  border-radius: 50%;
  cursor: pointer;
}
.Icon-check--active {
  background-color: #A4FF9C;
}
.button-edit, .Icon-delete {
  background: none;
  border: none;
  width: 30px;
}
.button-edit:hover {
  color: #344ada;
  cursor: pointer;
}
